.header {
    width: 100%; height: 3rem;
    position: fixed;
    z-index: 200;
    background-color: rgb(200, 230, 210);
}

.nav-bar-icon {
    display: none ;
}

@media (min-width:320px) and (max-width:760px) {
    .header {
        display: flex;
        justify-content: end;
        padding: 0 0.5rem;
        
    }
    .nav-bar-icon {
        font-size: 25px;
        padding: 0.5rem;
        display: flex;
        z-index: 100;

    }
}
